

import * as React from "react";
import { Check, ChevronsUpDown } from "lucide-react";

import { cn } from "@/lib/utils";
import {
  Command,
  CommandGroup,
  CommandItem,
  CommandList,
} from "@/components/ui/command";
import { Input } from "@/components/ui/input";
import type { FilterOption } from "./types";

export type FilterComboboxProps = {
  name: string;
  options?: FilterOption[];
  value?: string;
  onChange?: (value: string | undefined) => void;
  disabled?: boolean;
  readonly?: boolean;
};

export function FilterCombobox({
  name,
  options = [],
  value,
  onChange,
  disabled,
  readonly,
}: FilterComboboxProps) {
  const [open, setOpen] = React.useState(false);
  const [inputValue, setInputValue] = React.useState("");
  const containerRef = React.useRef<HTMLDivElement>(null);
  const inputRef = React.useRef<HTMLInputElement>(null);

  // value prop  inputValue 
  React.useEffect(() => {
    // 1.      
    const option = options.find((opt) => opt.value === value);
    if (option) {
        setInputValue(option.label);
    } else {
        // 2.   ?
        //  (Custom input )    ,   
        setInputValue(value || "");
    }
  }, [value, options]);

  //    ( )
  React.useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (containerRef.current && !containerRef.current.contains(event.target as Node)) {
        setOpen(false);
        
        //   ,        
        // (      )
        if (onChange && !disabled && !readonly) {
            //   value  
            // (inputValue label   value  . 
            //       ''  ,
            //    )
            
            const matchedOption = options.find(o => o.label === inputValue);
            const newValue = matchedOption ? matchedOption.value : inputValue;
            
            //   value   
            if (newValue !== value) {
                onChange(newValue === "" ? undefined : newValue);
            }
        }
      }
    };
    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, [inputValue, onChange, options, value, disabled, readonly]);


  const handleSelect = (currentValue: string) => {
    if (onChange) {
      onChange(currentValue);
    }
    setOpen(false);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newVal = e.target.value;
    setInputValue(newVal);
    if (!open) setOpen(true);
  };
  
  const filteredOptions = options.filter((option) =>
    option.label.toLowerCase().includes(inputValue.toLowerCase())
  );

  return (
    <div className="relative w-full" ref={containerRef}>
      <div className="relative">
        <Input
          ref={inputRef}
          value={inputValue}
          onChange={handleInputChange}
          onClick={() => !disabled && !readonly && setOpen(true)}
          onFocus={() => !disabled && !readonly && setOpen(true)}
          placeholder="..."
          className="w-full pr-8"
          disabled={disabled || readonly}
          autoComplete="off"
        />
        <ChevronsUpDown 
            className="absolute right-2 top-2.5 h-4 w-4 opacity-50 cursor-pointer" 
            onClick={() => {
                if (!disabled && !readonly) {
                    setOpen(!open);
                    inputRef.current?.focus();
                }
            }}
        />
      </div>
      
      {open && (
        <div className="absolute top-full left-0 z-50 mt-1 w-full min-w-[200px] rounded-md border bg-popover text-popover-foreground shadow-md outline-none animate-in fade-in-0 zoom-in-95">
          <Command>
            <CommandList className="max-h-[300px] overflow-auto">
                <CommandGroup>
                  {filteredOptions.length > 0 ? (
                      filteredOptions.map((option) => (
                        <CommandItem
                          key={option.value}
                          value={option.label}
                          onSelect={() => handleSelect(option.value)}
                          className="cursor-pointer"
                          onMouseDown={(e) => e.preventDefault()}
                        >
                          <Check
                            className={cn(
                              "mr-2 h-4 w-4",
                              value === option.value ? "opacity-100" : "opacity-0"
                            )}
                          />
                          {option.label}
                        </CommandItem>
                      ))
                  ) : (
                      <div className="py-2 text-center text-sm text-muted-foreground">
                        {inputValue ? "  " : " "}
                      </div>
                  )}
                </CommandGroup>
            </CommandList>
          </Command>
        </div>
      )}
    </div>
  );
}
